import { isEqual } from 'underscore';
import React from 'react';
import * as D3 from 'd3';
import ThemeContext from '../../contexts/ThemeContext';
/**
 * Generates an animated graph displaying locally stored stats
 * @memberof PanelBuildingBlocks
 */
class StatsGraph extends React.Component {
	static contextType = ThemeContext;

	/**
	 * Lifecycle event
	 */
	componentDidMount() {
		this.generateGraph();
	}

	/**
	 * Lifecycle event
	 */
	componentDidUpdate(prevProps) {
		if (!isEqual(prevProps, this.props)) {
			this.generateGraph();
		}
	}

	/**
	 * Generate line graph with the stats for the selected time slot
	 * Animate the graph whenever a render is triggered
	 * Add tooltips for each data point
	 */
	generateGraph() {
		const {
			data, demo, dailyOrMonthly, view, tooltipText
		} = this.props;

		// Clear graph
		D3.select(this.node).selectAll('*').remove();
		D3.select('.tooltip-container').selectAll('*').remove();

		// Add svg
		const margin = {
			top: 18, right: 40, bottom: 20, left: 70
		};
		const width = 560 - margin.left - margin.right;
		const height = 290 - margin.top - margin.bottom;

		const canvas = D3.select(this.node).append('svg')
			.attr('class', 'line-graph')
			.attr('width', width + margin.left + margin.right)
			// Add 15 pixels to the height of the SVG to make room for X axis labels
			.attr('height', height + margin.top + margin.bottom + 15)
			.append('g')
			.attr('transform', `translate(${margin.left},${margin.top})`);

		// Date formatting
		const parseMonth = D3.timeParse('%Y-%m-%d');

		let formatLabelDate;
		let formatTooltipDate;
		if (dailyOrMonthly === 'daily') {
			formatLabelDate = D3.timeFormat('%b %d');
			formatTooltipDate = D3.timeFormat('%b %d, %Y');
		} else {
			formatLabelDate = D3.timeFormat("%b '%y");
			formatTooltipDate = D3.timeFormat('%b %Y');
		}

		const dataJson = JSON.parse(JSON.stringify(data));
		dataJson.forEach((dataEntry) => {
			dataEntry.date = parseMonth(dataEntry.date);
		});

		let tickAmount;
		switch (dataJson.length) {
			case 0:
			case 1:
			case 6:
				tickAmount = dataJson.length;
				break;
			case 2:
			case 3:
			case 4:
			case 5:
				tickAmount = dataJson.length - 1;
				break;
			default:
				tickAmount = 6;
		}

		// Set scales
		const x = D3.scaleLinear()
			.range([0, width])
			.domain(D3.extent(dataJson, d => d.index));

		const y = D3.scaleLinear()
			.range([height, 0]);
		// ~ Handle axis styling for edge case of only one data point ~
		if (dataJson.length === 1) {
			y.domain([0, D3.max(dataJson, d => d.amount) * 2]);
		} else {
			y.domain(D3.extent(dataJson, d => d.amount));
		}

		// Add axes
		const xAxis = D3.axisBottom()
			.ticks(tickAmount)
			.tickSize(0)
			.tickFormat(d => formatLabelDate(dataJson[d].date))
			.scale(x);

		const yAxis = D3.axisLeft()
			.ticks(6)
			.tickSize(0)
			.scale(y);

		canvas.append('g')
			.attr('class', 'x axis')
			.attr('transform', `translate(0,${height})`)
			.call(xAxis);

		canvas.append('g')
			.attr('class', 'y axis')
			.call(yAxis);

		// Add grid
		canvas.append('g')
			.attr('class', 'grid')
			.call(yAxis.tickSize(-width).tickFormat(''));

		// Add data path
		const pathGroup = canvas.append('g').datum(dataJson);

		const line = D3.line()
			.x(d => x(d.index))
			.y(d => y(d.amount));

		// ---------------------------------------------------------------------- //
		// Animate data path using Mike Bostock's technique found here:
		// https://bl.ocks.org/mbostock/5649592
		// ---------------------------------------------------------------------- //
		function interpolator() {
			const l = this.getTotalLength();
			const i = D3.interpolateString(`0,${l}`, `${l},${l}`);
			return function(t) { return i(t); };
		}

		function animator(path) {
			path.transition()
				.duration(1000)
				.attrTween('stroke-dasharray', interpolator);
		}

		pathGroup.append('path')
			.style('stroke', '#dddddd')
			.style('stroke-dasharray', '4,4')
			.attr('d', line)
			.attr('fill', 'none');

		pathGroup.append('path')
			.attr('d', line)
			.attr('fill', 'none')
			.attr('class', `line ${this.context}`)
			.attr('stroke-width', 1.5)
			.call(animator);
		// ---------------------------------------------------------------------- //

		function toggleTooltip(context, index, selected, radius, clicked, duration, opacity) {
			setTimeout(() => {
				D3.select(context)
					.classed('selected', selected)
					.attr('r', radius);
				D3.select(`.tooltip-${index}`)
					.classed('clicked', clicked)
					.transition()
					.duration(duration)
					.style('opacity', opacity);
			}, 1);
		}
		// Add data points with event listeners for opening their respective tooltips
		canvas.append('g')
			.attr('class', 'point-group')
			.selectAll('circle')
			.data(dataJson)
			.enter()
			.append('circle')
			.attr('class', (d, i) => `point point-${i}`)
			.attr('class', this.context)
			.attr('cx', d => x(d.index))
			.attr('cy', d => y(d.amount))
			.attr('r', 0)
			.on('click', function(d, i) {
				if (!demo) {
					toggleTooltip(this, i, true, 6, true, 300, 1);
				}
			})
			.on('mouseenter', function(d, i) {
				if (!demo) {
					toggleTooltip(this, i, true, 6, false, 300, 1);
				}
			})
			.on('mouseleave', function(d, i) {
				if (!demo && !D3.select(`.tooltip-${i}`).classed('clicked')) {
					toggleTooltip(this, i, false, 4.5, false, 200, 0);
				}
			});

		// Add a tooltip to each data point
		if (!demo) {
			canvas.selectAll('circle')
				.each((d, i) => {
					let tooltipFlipped = false;
					let tooltipPositionX = x(d.index) + 22.5;
					let tooltipPositionY = Math.ceil(y(d.amount) - 12);

					if (tooltipPositionY < height / 2.5) {
						tooltipFlipped = true;
						tooltipPositionY += 130;
						tooltipPositionX += 0;
					} else if (view === 'trackersAnonymized') {
						tooltipPositionY -= 16;
					}

					const div = D3.select('.tooltip-container')
						.append('div')
						.attr('class', `tooltip tooltip-${i}`)
						.style('opacity', 0)
						.style('left', `${tooltipPositionX}px`)
						.style('top', `${tooltipPositionY}px`);

					if (view === 'trackersAnonymized') {
						div.classed('long-text', true);
					}

					div.append('div')
						.attr('class', tooltipFlipped ? 'pointer top under' : 'pointer bottom under');
					div.append('div')
						.attr('class', tooltipFlipped ? 'pointer top over' : 'pointer bottom over');

					div.append('p')
						.attr('class', 'tooltip-text-top')
						.html(`${formatTooltipDate(d.date)}<br/>${tooltipText}`);
					div.append('p')
						.attr('class', 'tooltip-text-bottom')
						.html(D3.format(',')(d.amount));
				});

			// Add event listener to container for closing tooltip
			D3.select('#content-stats')
				.on('click', () => {
					if (D3.event.target.className.baseVal !== 'point' &&
					D3.event.target.className !== 'tooltip') {
						D3.selectAll('.selected')
							.classed('selected', false)
							.attr('r', 4.5);
						D3.selectAll('.tooltip')
							.classed('clicked', false)
							.transition()
							.duration(200)
							.style('opacity', 0);
					}
				});
		}

		let additionalSeconds = 0;

		// Animate data points
		canvas.selectAll('circle')
			.each(function(d, i) {
				D3.select(this)
					.transition()
					.duration(700)
					.delay(i * 130 + additionalSeconds)
					.attr('r', 4.5);
				additionalSeconds += 20;
			});
	}

	/**
	 * React's required render function. Returns JSX
	 * @return {JSX} JSX for rendering the Historical Stats Graph in the Stats View component
	 */
	render() {
		const { timeframeSelectors, selectTimeframe } = this.props;
		return (
			<div className="line-graph-container">
				<div className="line-graph-ref" ref={(node) => { this.node = node; }} />
				<div className="tooltip-container" />
				<div
					id="stats-back"
					className={`caret ${timeframeSelectors.back}`}
					onClick={selectTimeframe}
				/>
				<div
					id="stats-forward"
					className={`caret ${timeframeSelectors.forward}`}
					onClick={selectTimeframe}
				/>
			</div>
		);
	}
}

export default StatsGraph;
