import { TOGGLE_EXPANDED } from '../constants/constants';

/**
 * Called from Detail and picked up by Panel reducer
 * @return {Object}
 */
export default function toggleExpanded() {
	return {
		type: TOGGLE_EXPANDED,
	};
}
